import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Group, Div, Cell, Avatar, Button, Link} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon16Verified from '@vkontakte/icons/dist/16/verified';


const osname = platform();

const Rgn = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="clans">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
		    Reign | Rgn <Icon16Verified/>
		</PanelHeader>
		<Group>
			<Div>
        <Cell
          photo="https://sun1.beltelecom-by-minsk.userapi.com/c831308/v831308917/fcbd6/KrLWdHBGxOc.jpg"
          description="Оффициальная группа"
          bottomContent={<Button  component="a" href="https://vk.com/rgnclan">Подписаться</Button>}
          before={<Avatar src="https://sun1.beltelecom-by-minsk.userapi.com/c831308/v831308917/fcbd6/KrLWdHBGxOc.jpg" size={80}/>}
          size="l"
        >
         Reign | Rgn
        </Cell>
			</Div>
		</Group>
        <Group title="Контакты">
            <Div>
Vk: <Link href="https://vk.com/rgnclan">@rgnclan</Link> <br />
Discord: <Link href="https://discordapp.com/invite/cjYMv59">discordapp.com/invite/cjYMv59</Link> <br />
Twitter: <Link href="https://twitter.com/CopsREIGN">Twitter.com/CopsREIGN</Link>
            </Div>
        </Group>
<Group title="Достижения">
              <Div>
               Первое место на CIS Pro League S5 <br />
Первое место на CIS Pro League S6 <br />
Победители MOBLIVE Invitational S1 <br />
Победители IFT EU Cup #9 <br />
Победители IFT EU Cup #15 <br />
Победители MST Pro League <br />
Победители SunScope Tournament S1 <br />
2 место на ESL Open Cup #4 Europe <br />
3 место на ESL Open Cup #3 Europe <br />
3 место на ESL Community Cup #6 <br />
Финалисты Европы на Team Liquid Tournament
              </Div>
            </Group>
	</Panel>

);

Rgn.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Rgn;