import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Group, Div, List, Cell, Avatar} from '@vkontakte/vkui';
import './Persik.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();



const Clans = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="home">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Кланы
		</PanelHeader>
		<Group>
			<Div>
				Список обновляется каждую неделю.
			</Div>
			</Group>
			<Group title="Список кланов." description="Если вы хотите чтобы тут появился и ваш клан, то пиши нам в личные сообщения.">
        <List>
          <Cell onClick={props.go} data-to="rgn" before={<Avatar src="https://sun1.beltelecom-by-minsk.userapi.com/c831308/v831308917/fcbd6/KrLWdHBGxOc.jpg" />} description="Тир 1">Reign | RGN</Cell>
        </List>
      </Group>
	</Panel>
);

Clans.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Clans;
